try {
  new window.ImageData(new Uint8ClampedArray([0, 0, 0, 0]), 1, 1);
} catch (e) {

  window.ImageData = function() {
    var args = arguments;

    if (args.length < 2) {
      throw new TypeError("Failed to construct 'ImageData': 2 arguments required, but only " + args.length + " present.");
    }

    if (args.length > 2) {

      if (!(args[0] instanceof Uint8ClampedArray)) {
        throw new TypeError("Failed to construct 'ImageData': 2 arguments required, but only " + args.length + " present.");
      }

      if (args[0].length !== 4 * args[1] * args[2]) {
        throw new Error("Failed to construct 'ImageData': The input data byte length is not a multiple of (4 * width * height)");
      }
    }

    var width = (args.length === 3) ? args[1]: args[0],
      height = (args.length === 3) ? args[2]: args[1],
      canvas = document.createElement('canvas'),
      ctx = canvas.getContext('2d'),
      imageData = ctx.createImageData(width, height);

    if (args.length === 3) imageData.data.set(args[0]);
    return imageData;
  };;
}
